





















































import { Component, Vue, Prop } from 'vue-property-decorator';

@Component
export default class PopDialog extends Vue {
  @Prop({
    type: String,
    default: 'inform'
  })
  dialogType!: string;

  @Prop({
    type: Boolean,
    default: false
  })
  showFlag!: Boolean;

  @Prop({
    type: Boolean,
    default: false
  })
  showCloseFlag!: Boolean;

  @Prop({
    type: Boolean,
    default: false
  })
  clickBgCloseFlag!: Boolean;

  @Prop({
    type: String,
    default: ''
  })
  title!: string;

  @Prop({
    type: Boolean, // 父组件传递给子组件的数据类型
    default: false // 默认值， 如果传入的是 Object，则要 default: ()=>({}) 参数为函数
  })
  centerStyle!: Boolean;

  @Prop({
    type: String,
    default: '取消'
  })
  cancelbuttonText!: string;

  @Prop({
    type: String, // 父组件传递给子组件的数据类型
    default: '确定' // 默认值， 如果传入的是 Object，则要 default: ()=>({}) 参数为函数
  })
  buttonText!: string;

  @Prop({
    type: Boolean,
    default: false
  })
  sureButtonDisabled!: Boolean;

  @Prop({
    type: String,
    default: 'primary'
  })
  sureButtonType!: string;

  @Prop({
    type: Number // 父组件传递给子组件的数据类型
  })
  width!: number;

  @Prop({
    type: Number // 父组件传递给子组件的数据类型
  })
  height!: number;

  clickBtn(type: string) {
    if (type === 'sure') {
      this.$emit('sureClick');
    }
    if (type === 'cancel') {
      this.$emit('cancelClick');
    }
  }

  clickBg() {
    if (this.clickBgCloseFlag) {
      this.hideIt();
    }
  }

  hideIt() {
    this.$emit('update:showFlag', false);
  }
}
