import materialContent from './materialContent.vue';
import materialLibrary from './materialLibrary.vue';
import folderAside from './folderAside.vue';
import materialUpload from './upload.vue';

interface VueComp {
  install?: Function;
}

(materialContent as VueComp).install = (vue: any) => {
  vue.component('materialContent', materialContent);
};
(materialLibrary as VueComp).install = (vue: any) => {
  vue.component('materialLibrary', materialLibrary);
};
(folderAside as VueComp).install = (vue: any) => {
  vue.component('folderAside', folderAside);
};
(materialUpload as VueComp).install = (vue: any) => {
  vue.component('materialUpload', materialUpload);
};

const install = (vue: any) => {
  vue.component('materialLibrary', materialLibrary);
  vue.component('folderAside', folderAside);
  vue.component('materialContent', materialContent);
  vue.component('materialUpload', materialUpload);
};

export default {
  install,
  materialLibrary,
  materialContent,
  folderAside,
  materialUpload
};
