





















































































































































import { Component, Vue, Prop } from 'vue-property-decorator';
import rightClickMenu from './rightClickMenu.vue';
import popDialog from './popDialog.vue';
import folderTree from './folderTreeDialog.vue';
import { normalizeNameFun } from '../common/js/config';
import { FolderNode } from '../common/js/typing';

interface treeData {
  name: string;
  quantity: number;
  children?: Array<FolderNode>;
}

@Component({
  name: 'folderAside',
  components: {
    rightClickMenu,
    popDialog,
    folderTree
  }
})
export default class folderAside extends Vue {
  $refs!: {
    addTopFolderInputDom: HTMLElement;
    downLoad: any;
  };

  @Prop({
    type: Array,
    default: []
  })
  permissionList!: Array<string>;

  @Prop({
    type: Object,
    default() {
      return {};
    }
  })
  totalFolder!: treeData;

  @Prop({
    type: Object,
    default() {
      return {};
    }
  })
  treeFolder!: treeData;

  @Prop({
    type: Boolean,
    default() {}
  })
  loading!: boolean;

  isShowNewFolderInput: boolean = false;

  chosenFolderId: number = -1;

  showSearchPannel: boolean = false;

  initTree: boolean = false;

  curFolderId: number = parseInt(localStorage.getItem('curFolderId') || '0', 10);

  curHandleFolderItem: FolderNode | string = ''; // 当前正进行操作的文件夹

  showDialogFlag: boolean = false; // 是否显示文件夹操作弹窗

  dialogTitle: string = ''; // 文件夹操作弹窗标题

  searchName: string = '';

  dialogFolderName: string = ''; // 文件夹操作弹窗输入框值

  chooseFolderType: string = '';

  canAddTopFolder: boolean = true;

  showChooseFolderDialog: boolean = false;

  newFolderName: string = ''; // 新建文件夹输入框值

  get trueTreeFolder() {
    const result = this.treeFolder.children || [];
    this.$parent.$data.treeFolder = result;
    return result;
  }

  get folderMenuList() {
    const permissionMenu: any = {
      addFolder: '创建子文件夹',
      renameFolder: '对文件夹进行重命名',
      downloadFolder: '导出文件夹',
      delFolder: '删除文件夹',
      moveFolder: '移动文件夹'
    };
    const result: Array<string> = [];
    this.permissionList.forEach(item => {
      if (permissionMenu[item]) {
        result.push(permissionMenu[item]);
      }
    });
    return result;
  }

  // 当前是否处于文件夹页面
  get isInFolder() {
    const result = this.curFolderId && this.curFolderId !== 0;
    return result;
  }

  get canNewTopFolder() {
    return this.permissionList.indexOf('addTopFolder') > -1;
  }

  get folderPathArr() {
    const name = this.searchName;
    // let searchArr = [];
    if (name.length > 0) {
      const folderArr = this.trueTreeFolder;
      const checkFolders = this.getCheckFolders({}, folderArr, name, []);
      const folderPathArr: any = [];
      // 获取到所有上级文件夹
      for (let i = 0; i < checkFolders.length; i += 1) {
        folderPathArr.push({
          path: this.getFolderPath(checkFolders[i]),
          id: checkFolders[i].id
        });
      }
      if (folderPathArr.length > 0) {
        return folderPathArr;
      }
      return [
        {
          path: '没有找到该目录',
          id: -1
        }
      ];
    }
    return [];
  }

  getIconClass(item: FolderNode) {
    if (item.name === '全部素材') {
      return 'iconfont icon-quanbusucai';
    }
    return '';
  }

  getChosenFolderId(id: number) {
    this.chosenFolderId = id;
  }

  confirmFolderDialog() {
    this.initTree = false;
    if (this.chosenFolderId !== -1) {
      // 移动到文件夹
      this.$emit('moveFolder', this.curFolderId, this.chosenFolderId, (value: string) => {
        if (value === 'success') {
          this.showChooseFolderDialog = false;
        }
      });
    } else {
      this.$message({
        message: '请选择文件夹',
        type: 'warning'
      });
    }
  }

  handleNodeClick(item: FolderNode) {
    this.curFolderId = item.id || 0;
    localStorage.setItem('curFolderId', `${this.curFolderId}`);
    this.$parent.$data.curFolderId = this.curFolderId;
    this.$parent.$data.curFolderName = item.name;
    this.$emit('getSelectFolder', item.id, item.name);
  }

  // 创建文件夹（一级和子级）、对文件夹进行重命名（一级和子级）、删除文件夹（一级和子级
  // 后都要重新调用查询文件夹目录接口
  handleClickMenu(node: FolderNode, menu: string) {
    this.curHandleFolderItem = node;
    if (menu === '创建子文件夹') {
      this.showDialogFlag = true;
      this.dialogTitle = '创建子文件夹';
      this.dialogFolderName = '';
    } else if (menu === '对文件夹进行重命名') {
      this.showDialogFlag = true;
      this.dialogTitle = '对文件夹进行重命名';
      this.dialogFolderName = '';
    } else if (menu === '移动文件夹') {
      this.initTree = true;
      this.showChooseFolderDialog = true;
      this.chooseFolderType = '移动文件夹';
    } else if (menu === '删除文件夹') {
      this.deleteFolder();
    } else if (menu === '导出文件夹') {
      this.exportFolder();
    }
  }

  inputFn() {
    if (this.searchName.length > 0) {
      this.showSearchPannel = true;
    } else {
      this.showSearchPannel = false;
    }
  }

  // 创建子文件夹 或 对文件夹进行重命名
  confirmDialog() {
    let getName = this.dialogFolderName;
    if (getName.trim().length === 0) {
      this.$message({
        message: '文件夹名称不能为空',
        type: 'error'
      });
      this.dialogFolderName = '';
    } else {
      getName = normalizeNameFun(getName);
      const getItem = this.curHandleFolderItem as FolderNode;
      if (this.dialogTitle === '创建子文件夹') {
        this.$emit('addFolder', getName, getItem.id, (value: string) => {
          if (value === 'success') {
            this.showDialogFlag = false;
          }
        });
      }
      if (this.dialogTitle === '对文件夹进行重命名') {
        const getId = getItem.id;
        if (getId) {
          this.$emit('renameFolder', getName, getId, (value: string) => {
            if (value === 'success') {
              this.showDialogFlag = false;
            }
          });
        }
      }
    }
  }

  getCheckFolders(
    parent: any,
    folderArr: Array<FolderNode>,
    name: string,
    result: Array<FolderNode>
  ) {
    for (let i = 0; i < folderArr.length; i += 1) {
      const arr = folderArr[i];
      arr.parent = parent;
      if (arr.name.indexOf(name) >= 0) {
        result.push(arr);
      }
      if (arr.children) {
        this.getCheckFolders(arr, arr.children || [], name, result);
      }
    }
    return result;
  }

  getFolderPath(checkFolders: FolderNode) {
    const stack = [];
    let path = '';
    stack.push(checkFolders);
    while (stack.length > 0) {
      const arr = stack.pop();
      path += `/${arr && arr.name}`;
      if (arr && arr.parent?.name) {
        stack.push(arr.parent);
      }
    }
    return path
      .split('/')
      .splice(1)
      .reverse()
      .join('/');
  }

  // 获取文件夹id
  getCurFolderId(id: number, path: string) {
    this.showSearchPannel = false;
    this.curFolderId = id;
    const name = path !== '没有找到该目录' ? path.split('/').pop() : '';
    this.searchName = name || '';
    this.$parent.$data.curFolderId = this.curFolderId;
    localStorage.setItem('curFolderId', `${this.curFolderId}`);
    this.$parent.$data.curFolderName = name;
    this.$emit('getSelectFolder', id, name);
  }

  // 删除文件夹
  deleteFolder() {
    this.$confirm(
      '删除文件夹将会删除文件夹下的所有文件及子文件夹。确认要删除该文件夹吗？',
      '提示',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }
    )
      .then(() => {
        const getItem = this.curHandleFolderItem as FolderNode;
        const getId = getItem.id;
        if (getId) {
          this.$emit('delFolder', getId);
        }
      })
      .catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
  }

  // 导出文件夹
  exportFolder() {
    const that = this;
    const getItem = this.curHandleFolderItem as FolderNode;
    const getId = getItem.id;
    // 为文件夹才可导出
    if (getId) {
      const quantityF = getItem.quantity;
      if (quantityF === 0) {
        that.$message({
          message: '该文件夹无素材',
          type: 'error'
        });
      } else {
        // 打开下载面板
        // this.$parent.$data.showDownloadPanel = true;
        this.$emit('exportFolder', getId);
      }
    }
  }

  showAddTopFolderInput() {
    this.isShowNewFolderInput = true;
    this.newFolderName = '';
    this.$nextTick(() => {
      this.$refs.addTopFolderInputDom.focus();
    });
  }

  hideAddTopFolderInput() {
    this.isShowNewFolderInput = false;
    this.newFolderName = '';
    this.canAddTopFolder = true;
  }

  // 创建一级文件夹
  addTopFolder() {
    if (this.canAddTopFolder) {
      this.canAddTopFolder = false;
      let getNewFolderName = this.newFolderName;
      if (getNewFolderName.trim().length === 0) {
        this.$message({
          message: '名字不能为空',
          type: 'error'
        });
        this.canAddTopFolder = true;
      } else {
        getNewFolderName = normalizeNameFun(getNewFolderName);
        this.$emit('addFolder', getNewFolderName, 0, (value: string) => {
          setTimeout(() => {
            this.canAddTopFolder = true;
          }, 200);
          if (value === 'success') {
            this.isShowNewFolderInput = false;
          }
        });
      }
    }
  }
}
