






































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { defaultIconPicConfig, lenEllipsisFun, normalizeSizeFun } from '../common/js/config';
import { MaterialFile } from '../common/js/typing';

@Component
export default class DownloadPanel extends Vue {
  @Prop({
    type: Boolean
  })
  showDownloadPanel!: boolean;

  @Prop({
    type: Boolean,
    default: false
  })
  isHideDownloadPanel!: boolean;

  @Prop({
    type: Array
  })
  downloadFileList!: Array<MaterialFile>;

  @Prop({
    type: Object
  })
  downloadFile!: MaterialFile;

  successDownloadFileList: Array<MaterialFile> = [];

  isHidePanel: boolean = false;

  failDownloadFileList: Array<MaterialFile> = [];

  get successDownloadNum() {
    return this.successDownloadFileList.length;
  }

  get failDownloadNum() {
    return this.failDownloadFileList.length;
  }

  get downloadNum() {
    return this.downloadFileList.length;
  }

  get downloadPercentage() {
    return (this.successDownloadNum / this.downloadNum) * 100;
  }

  get isAllSuccess() {
    return this.successDownloadNum === this.downloadNum;
  }

  @Watch('isHideDownloadPanel')
  onIsHidePanelChange(val: boolean) {
    // 更新状态
    this.isHidePanel = val;
  }

  @Watch('downloadFileList', { immediate: true, deep: true })
  onValueChange() {
    // 更新状态
    this.downloadFileStatus();
  }

  @Watch('downloadFile.name', { immediate: true, deep: true })
  onFileChange() {
    if (this.downloadFile.url !== '') {
      this.downloadFileFun(this.downloadFile);
    }
  }

  getIconPic(format: string) {
    return defaultIconPicConfig[format.toUpperCase()];
  }

  formatLen(value: string, limit: number) {
    return lenEllipsisFun(value, limit);
  }

  formatSize(size: string) {
    return normalizeSizeFun(size);
  }

  downloadFileStatus() {
    const getUList = this.downloadFileList;
    this.successDownloadFileList = getUList.filter(f => f.status === 'success');
    this.failDownloadFileList = getUList.filter(f => f.status === 'fail');
  }

  // 格式化上传进度
  formatProgress(progress: number) {
    if (progress) {
      return `${Number.parseFloat(progress.toFixed(2))}%`;
    }
    return '排队中...';
  }

  closeDownPanel() {
    this.$emit('close');
  }

  downloadFileFun(file: MaterialFile) {
    if (file.url) {
      this.getBlob(file.url, file).then(blob => {
        const link = document.createElement('a');
        link.href = window.URL.createObjectURL(blob);
        link.download = file.name || '';
        link.style.display = 'none';
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        window.URL.revokeObjectURL(link.href);
      });
    }
  }

  getBlob(url: string, fileData: MaterialFile) {
    return new Promise(resolve => {
      const xhr = new XMLHttpRequest();
      const file = fileData;
      xhr.open('GET', url, true);
      xhr.responseType = 'blob';
      xhr.onload = () => {
        if (xhr.status === 200) {
          this.$set(file, 'status', 'success');
          this.$set(file, 'url', window.URL.createObjectURL(xhr.response));
          resolve(xhr.response);
        } else {
          this.$set(file, 'status', 'fail');
          this.$set(file, 'reason', '下载失败,请重试');
        }
      };
      xhr.onerror = e => {
        this.$set(file, 'status', 'fail');
        this.$set(file, 'reason', e || '下载失败,请重试');
      };
      xhr.onprogress = (evt: any) => {
        file.progress = (parseFloat(evt.loaded) / parseFloat(evt.total)) * 100;
        file.size = evt.total;
      };
      xhr.send();
    });
  }
}
