interface pointData {
  x: number;
  y: number;
}

export default class Drag {
  oBorder: HTMLElement;

  oLeft: HTMLElement;

  oRight: HTMLElement;

  oStartPoint: pointData;

  width: number;

  oMovingPoint: pointData;

  bMoving: Boolean;

  offsetLeft: number;

  constructor(wrapper: string, border: string, left: string, right: string) {
    this.oBorder = document.getElementById(border) as HTMLElement;
    this.oLeft = document.getElementById(left) as HTMLElement;
    this.oRight = document.getElementById(right) as HTMLElement;
    this.width = document.getElementById(wrapper)?.clientWidth || 0;
    this.offsetLeft = document.getElementById(wrapper)?.offsetLeft || 0;
    this.oStartPoint = {
      x: 0,
      y: 0
    };
    this.oMovingPoint = {
      x: 0,
      y: 0
    };
    this.bMoving = false;
  }

  init() {
    if (this.oBorder) {
      this.oBorder.onmousedown = () => {
        this.startMove();
        return false;
      };
    }

    document.onmousemove = (e: MouseEvent) => {
      const evt = e;
      this.moving(evt);
      return false;
    };
    document.onmouseup = () => {
      this.mouseUp();
      return false;
    };
  }

  startMove() {
    if (!this.bMoving) {
      this.bMoving = true;
    }
  }

  moving(e: MouseEvent) {
    if (this.bMoving) {
      const x = e.clientX - this.offsetLeft;
      if (x >= 200 && x < 600) {
        this.oMovingPoint = { x, y: e.clientY };
        this.oBorder.style.left = `${this.oMovingPoint.x}px`;
        this.oLeft.style.width = `${this.oMovingPoint.x}px`;
        const rightWidth = this.width - this.oMovingPoint.x;
        this.oRight.style.width = `${rightWidth}px`;
      }
    }
  }

  mouseUp() {
    if (this.bMoving) {
      this.bMoving = false;
    }
  }
}
