


















import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { ElMessage } from 'element-ui/types/message';
import {
  availableSuffix,
  videoSuffix,
  audioSuffix,
  normalizeNameFun,
  normalizeDurationFun,
  defaultIconPicConfig
} from '../common/js/config';

import { MaterialFile, MaterialItem } from '../common/js/typing';

interface ExistFile {
  originFile: MaterialItem;
  existFile: MaterialFile;
}

interface MessageInstance extends ElMessage {
  closeAll: Function;
}

@Component({
  components: {}
})
export default class upload extends Vue {
  $refs!: {
    uploadDom: any;
  };

  @Prop()
  curFolderId!: number;

  @Prop({
    type: String,
    default: ''
  })
  curFolderName!: string;

  @Prop({
    type: Boolean,
    default: false
  })
  isDrag!: boolean;

  // 表示组件内部区域是否允许点击
  @Prop({
    type: Boolean,
    default: true
  })
  canClick!: boolean;

  @Prop({
    type: Boolean,
    default: false
  })
  disabled!: boolean;

  @Prop({
    type: Boolean,
    default: true
  })
  canUpload!: boolean;

  get isInFolder() {
    const result = this.curFolderId && this.curFolderId !== 0;
    return result;
  }

  showExistDialog: boolean = false;

  existFileArr: Array<ExistFile> = [];

  replaceExistDecision: string = ''; // 用户是否替换选择

  saveClickFun!: Function; // 暂存上传组件的点击响应方法，默认会emit，父组件可取值后控制点击响应

  @Watch('canClick')
  onCanClickChange() {
    this.handleBanClick();
  }

  created() {}

  mounted() {
    this.handleBanClick();
  }

  updated() {
    this.handleBanClick();
  }

  handleBanClick() {
    if (this.saveClickFun) {
      if (this.canClick) {
        this.$refs.uploadDom.$refs['upload-inner'].handleClick = this.saveClickFun;
      } else {
        this.$refs.uploadDom.$refs['upload-inner'].handleClick = () => false;
      }
    } else {
      this.saveClickFun = this.$refs.uploadDom.$refs['upload-inner'].handleClick;
      this.$emit('emitClickFun', this.saveClickFun);
    }
  }

  // todo: 上传单个文件大小是否限制
  beforeFileUpload(file: any) {
    if (!this.canUpload) {
      (this.$message as MessageInstance).closeAll();
      this.$message.error('您没有上传素材的权限');
      return false;
    }
    if (!this.isInFolder) {
      (this.$message as MessageInstance).closeAll();
      this.$message.warning('请选择文件夹后再上传');
      return false;
    }
    const suffix = file.name.substr(file.name.lastIndexOf('.') + 1).toUpperCase();
    if (availableSuffix.indexOf(suffix) === -1) {
      this.$notify({
        title: '错误',
        message: `${file.name}格式不正确，无法上传`,
        duration: 0
      });
      return false;
    }
    // 获取文件基本信息
    const { name, size } = file;
    const fName = normalizeNameFun(name.substr(0, name.lastIndexOf('.')));
    const ext = name.substr(name.lastIndexOf('.') + 1);
    // 当前上传文件(TODO: 优化通过修改file对象方式传递数据)
    const getFile = file;
    getFile.uploadInfo = {
      pid: this.curFolderId,
      folderName: this.curFolderName,
      name: fName,
      ext,
      size: `${size}`,
      material_real_size: '',
      video_duration: '',
      status: 'uploading',
      reason: '',
      progress: 0,
      checkpoint: ''
    };
    // 音频信息：时长 前端获取
    if (audioSuffix.indexOf(ext.toUpperCase()) !== -1) {
      const url = URL.createObjectURL(getFile);
      const tempAudio = document.createElement('audio');
      tempAudio.setAttribute('crossOrigin', 'anonymous'); // 处理跨域
      tempAudio.setAttribute('src', url);
      tempAudio.addEventListener('loadedmetadata', () => {
        getFile.uploadInfo.video_duration = normalizeDurationFun(tempAudio.duration);
      });
    }
    // 视频信息尺寸 和 时长 前端获取(其他由后台获取)
    if (videoSuffix.indexOf(ext.toUpperCase()) !== -1) {
      const url = URL.createObjectURL(getFile);
      const tempVideo = document.createElement('video');
      tempVideo.setAttribute('crossOrigin', 'anonymous'); // 处理跨域
      tempVideo.setAttribute('src', url);
      tempVideo.addEventListener('loadedmetadata', () => {
        getFile.uploadInfo.video_duration = normalizeDurationFun(tempVideo.duration);
        const { videoWidth, videoHeight } = tempVideo;
        getFile.uploadInfo.material_real_size = `${videoWidth}x${videoHeight}`;
      });
    }
    return true;
  }

  // 导入并替换
  sureUpload() {
    this.showExistDialog = false;
    this.existFileArr = [];
    this.replaceExistDecision = 'yes';
  }

  // 不要导入
  cancelUpload() {
    this.showExistDialog = false;
    this.existFileArr = [];
    this.replaceExistDecision = 'no';
  }

  // 直传至oss
  ossUpload(param: any) {
    this.$emit('uploadFile', param);
  }

  getPreviewUrl(item: MaterialItem) {
    if (item.format.toUpperCase() === 'GIF') {
      return item.original_url;
    }
    return item.preview_url;
  }

  getIconPic(ext: string) {
    return defaultIconPicConfig[ext.toUpperCase()];
  }
}
