








































import { Component, Vue, Prop } from 'vue-property-decorator';
import { defaultIconPicConfig } from '../common/js/config';
import { MaterialItem } from '../common/js/typing';

@Component
export default class previewPanel extends Vue {
  @Prop()
  previewList!: Array<MaterialItem>;

  @Prop()
  previewIndex!: number;

  nowSrc: string = '';

  nowType: string = '';

  nowName: string = '';

  thisID: number = -1;

  loading: boolean = true;

  get isFirstIndex() {
    return this.thisID === 0;
  }

  get isLastIndex() {
    return this.thisID === this.previewList.length - 1;
  }

  get isAudio() {
    return this.nowType === '音频';
  }

  get isVideo() {
    return this.nowType === '视频';
  }

  get isImg() {
    if (this.previewList[this.thisID].format.toUpperCase() === 'PSB') {
      return false;
    }
    return this.nowType === '图片';
  }

  created() {
    this.thisID = this.previewIndex;
    const getPItem = this.previewList[this.thisID];
    this.nowSrc = getPItem.original_url;
    this.nowType = getPItem.type;
    this.nowName = `${getPItem.name}.${getPItem.format}`;
  }

  mounted() {}

  getIconPic() {
    return defaultIconPicConfig[this.previewList[this.thisID].format.toUpperCase()];
  }

  toNext() {
    if (this.thisID < this.previewList.length - 1) {
      const getPNextItem = this.previewList[this.thisID + 1];
      this.nowSrc = getPNextItem.original_url;
      this.nowType = getPNextItem.type;
      this.nowName = `${getPNextItem.name}.${getPNextItem.format}`;
      this.thisID += 1;
    }
  }

  toPrev() {
    if (this.thisID > 0) {
      const getPPrevItem = this.previewList[this.thisID - 1];
      this.nowSrc = getPPrevItem.original_url;
      this.nowType = getPPrevItem.type;
      this.nowName = `${getPPrevItem.name}.${getPPrevItem.format}`;
      this.thisID -= 1;
    }
  }

  close() {
    this.$emit('closePreview');
  }
}
