






























import { Component, Vue, Prop } from 'vue-property-decorator';
import Drag from '../common/js/Drag';
import { MaterialFile, FolderNode } from '../common/js/typing';
import DownloadPanel from './downloadPanel.vue';
import UploadPanel from './uploadPanel.vue';

@Component({
  name: 'materialLibrary',
  components: {
    DownloadPanel,
    UploadPanel
  }
})
export default class materialLibrary extends Vue {
  $refs!: {
    panelListDom: HTMLElement;
    folderAside: any;
  };

  @Prop({
    type: String,
    default: 0
  })
  height!: string;

  tableHeight: number = 0;

  isInFolder: boolean = false;

  treeFolder: Array<FolderNode> = [];

  curFolderId: number = parseInt(localStorage.getItem('curFolderId') || '', 10) || 0; // 当前选择的文件夹id

  curFolderName: string = '';

  selectAllIds: boolean = false;

  selectNoneIds: boolean = false;

  mounted() {
    const drag = new Drag('wrapper', 'border', 'left', 'right');
    drag.init();
    this.$nextTick(() => {
      this.tableHeight = this.$refs.folderAside.clientHeight;
    });
  }

  openDownloadPanel() {
    this.showDownloadPanel = true;
    this.isHidePanel = true;
    // 当弹框处于收起状态，添加上传任务时，弹框自动弹起
    if (this.isHideDownloadPanel) {
      this.isHideDownloadPanel = false;
    }
  }

  openUploadPanel() {
    this.showUploadPanel = true;
    // 当弹框处于收起状态，添加上传任务时，弹框自动弹起
    if (this.isHidePanel) {
      this.isHidePanel = false;
    }
    this.isHideDownloadPanel = true;
  }

  setDownloadFiles(file: MaterialFile) {
    this.openDownloadPanel();
    this.downloadFileList.push(file);
    setTimeout(() => {
      this.downloadFile = file;
    }, 0);
    // 上传面板显示到最新任务上传位置
    this.scrollToBottom();
  }

  setDownloadList(files: Array<MaterialFile>) {
    this.openDownloadPanel();
    files.forEach((item: MaterialFile, index: number) => {
      this.$set(this.downloadFileList, index, item);
    });
  }

  setUploadList(files: Array<MaterialFile>) {
    this.openUploadPanel();
    files.forEach((item: MaterialFile, index: number) => {
      this.$set(this.uploadFileList, index, item);
    });
  }

  setUploadFiles(file: MaterialFile) {
    this.openUploadPanel();
    this.uploadFileList.push(file);
    this.uploadFile = file;
    this.scrollToBottom();
  }

  scrollToBottom() {
    this.$nextTick(() => {
      setTimeout(() => {
        const listDom = this.$refs.panelListDom;
        if (listDom) {
          listDom.scrollTop = listDom.scrollHeight;
        }
      }, 500);
    });
  }

  selectAll() {
    this.selectAllIds = !this.selectAllIds;
  }

  selectNone() {
    this.selectNoneIds = !this.selectNoneIds;
  }

  downloadFileList: Array<MaterialFile> = [];

  uploadFileList: Array<MaterialFile> = [];

  downloadFile: MaterialFile = {
    name: '', // 文件名
    ext: '', // 文件格式
    size: '' // 文件大小 单位byt
  };

  uploadFile: MaterialFile = {
    name: '', // 文件名
    ext: '', // 文件格式
    size: '' // 文件大小 单位byt
  };

  showDownloadPanel: boolean = false;

  showUploadPanel: boolean = false;

  isHideDownloadPanel: boolean = false;

  isHidePanel: boolean = false;

  closeDownPanel() {
    this.$emit('closeDownLoadPannel');
    this.showDownloadPanel = false;
  }

  closePanel() {
    this.$emit('closeUpLoadPannel');
    this.showUploadPanel = false;
  }
}
