

































































































































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import {
  defaultIconPicConfig,
  normalizeSizeFun,
  isHavePreviewUrl,
  videoSuffix,
  audioSuffix
} from '../common/js/config';
import { MaterialItem, NestTagOptions, TagItem } from '../common/js/typing';

@Component
export default class detailDrawer extends Vue {
  $refs!: {
    chooseTagBoxDom: any;
  };

  @Prop()
  detailMaterialItem!: MaterialItem;

  @Prop()
  openDetailFLag!: boolean;

  @Prop()
  openDetailIdArr!: number[];

  @Prop()
  openDetailType!: string;

  @Prop({
    type: Array,
    default: []
  })
  optionsTags!: Array<NestTagOptions>;

  editName: string = '';

  showChooseTagBoxFlag: boolean = false;

  chooseTag: Array<Array<number>> = [];

  cascaderProps: object = {
    value: 'id',
    label: 'name'
  };

  recentTags: Array<TagItem> = [];

  loadingRecentTags: boolean = false;

  loadingTags: boolean = false;

  @Watch('detailMaterialItem')
  onMaterialItemChange(newVal: MaterialItem) {
    if (newVal) {
      this.editName = this.detailMaterialItem.name;
    }
  }

  get IsAddTag() {
    return this.openDetailType === 'tag';
  }

  get folderTags() {
    return this.detailMaterialItem.folder_name.split(',');
  }

  checkPreview(format: string) {
    return isHavePreviewUrl(format);
  }

  getPreviewUrl(item: MaterialItem) {
    if (item.format?.toUpperCase() === 'GIF') {
      return item.original_url;
    }
    return item.preview_url;
  }

  checkCanPlay(format: string) {
    return videoSuffix.indexOf(format?.toUpperCase()) > -1 ||
           audioSuffix.indexOf(format?.toUpperCase()) > -1;
  }

  getIconPic(format: string) {
    return defaultIconPicConfig[format?.toUpperCase()];
  }

  formatSize(size: string) {
    return normalizeSizeFun(size);
  }

  rename() {
    if (this.editName !== this.detailMaterialItem.name) {
      this.$emit('rename', this.editName, this.detailMaterialItem);
    }
  }

  previewThis() {
    this.$emit('previewThis', this.detailMaterialItem);
  }

  handleClickTagBox() {
    this.showChooseTagBoxFlag = !this.showChooseTagBoxFlag;
    if (this.showChooseTagBoxFlag) {
      this.getRecentTags();
    }
    // 点击空白区域隐藏
    setTimeout(() => {
      document.documentElement.addEventListener('click', this.hideChooseTagBox);
    }, 300);
  }

  hideChooseTagBox(e: MouseEvent) {
    const getDom = this.$refs.chooseTagBoxDom;
    if (getDom && !getDom.contains(e.target)) {
      this.showChooseTagBoxFlag = false;
      setTimeout(() => {
        document.documentElement.removeEventListener('click', this.hideChooseTagBox);
      }, 300);
    }
  }

  handleChooseTag(value: Array<number>) {
    this.handleSetTag(value, 'add');
    // 清空选择框值
    this.chooseTag = [];
  }

  handleSetTag(value: Array<number>, type: string) {
    // 添加标签
    if (type === 'add') {
      // 获取选择要添加的
      const addId = value[value.length - 1];
      // 获取已有的
      const tag_ids = this.detailMaterialItem.tag_info.map(t => t.id) || [];
      if (addId !== undefined) {
        if (tag_ids.indexOf(addId) === -1) {
          tag_ids.push(addId);
          this.loadingTags = true;
          this.$emit('setTag', tag_ids, this.openDetailIdArr, (newTagInfo: Array<TagItem>) => {
            // 添加成功后，本地更新标签数据
            if (newTagInfo && this.detailMaterialItem && typeof this.detailMaterialItem === 'object') {
              this.$set(this.detailMaterialItem, 'tag_info', newTagInfo);
              this.$forceUpdate();
            }
            this.loadingTags = false;
          });
        } else {
          // 已重复的不添加
          this.$message({
            type: 'info',
            message: '该标签已存在'
          });
        }
      }
    }
    // 移除标签
    // if (type === 'del') {}
  }

  getRecentTags() {
    this.loadingRecentTags = true;
    this.recentTags = [];
    this.$emit('getRecentTags', (getTags: Array<TagItem>) => {
      if (getTags !== undefined) {
        this.recentTags = getTags.slice();
        this.$forceUpdate();
      }
      this.loadingRecentTags = false;
    });
  }

  clickBg() {
    this.$emit('closeDetail');
  }
}
