const havePreviewFormat = ['JPG', 'JPEG', 'PNG', 'GIF', 'MP4', 'MOV'];

export function isHavePreviewUrl(format: string) {
  return havePreviewFormat.indexOf(format?.toUpperCase()) > -1;
}

// 规范格式化命名格式
export function normalizeNameFun(name: string) {
  let result = name;
  // 去掉空格
  result = result.replace(/\s*/g, '');
  // /\:*?"<>| 转化为 _
  result = result.replace(/\/|\\|:|\*|\?|"|<|>|\|/g, '_');
  return result;
}

function completeDigit(num: number) {
  return num < 10 ? `0${num}` : `${num}`;
}

/**
 * 长度省略
 * @param {string} value 原内容
 * @param {string} limit 限制字数（多少字符后省略）
 */
export function lenEllipsisFun(value: string, limit: number) {
  return value.length > limit ? `${value.substring(0, limit)}...` : value;
}

/**
 * 视频时长格式
 * @param {number} duration 时长
 */
export function normalizeDurationFun(duration: number) {
  const result = parseInt(`${duration}`, 10);
  const hour = completeDigit(Math.floor(result / 3600));
  const other = result % 3600;
  const minute = completeDigit(Math.floor(other / 60));
  const second = completeDigit(parseFloat((other % 60).toFixed(2)));
  return `${hour}:${minute}:${second}`;
}

/**
 * 大小单位转化 ---- 默认由字节转化为兆
 * @param {string} size 大小，默认单位（字节）
 */
export function normalizeSizeFun(size: string) {
  return `${(Number(size) / 1024 / 1024).toFixed(2)}MB`;
}
