








































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { defaultIconPicConfig, lenEllipsisFun, normalizeSizeFun } from '../common/js/config';
import { MaterialFile } from '../common/js/typing';

@Component
export default class UploadPanel extends Vue {
  @Prop({
    type: Boolean,
    default: false
  })
  showUploadPanel!: boolean;

  @Prop({
    type: Boolean,
    default: false
  })
  isHideUpLoadPanel!: boolean;

  @Prop({
    type: Array
  })
  uploadFileList!: Array<MaterialFile>;

  @Prop({
    type: Object
  })
  uploadFile!: MaterialFile;

  successUploadFileList: Array<MaterialFile> = [];

  isHidePanel: boolean = false;

  failUploadFileList: Array<MaterialFile> = [];

  get successNum() {
    return this.successUploadFileList.length;
  }

  get failNum() {
    return this.failUploadFileList.length;
  }

  get uploadNum() {
    return this.uploadFileList.length;
  }

  get uploadPercentage() {
    return (this.successNum / this.uploadNum) * 100;
  }

  get isAllSuccess() {
    return this.successNum === this.uploadNum;
  }

  @Watch('isHideUpLoadPanel', { immediate: true, deep: true })
  onIsHideUpLoadPanelChange(val: boolean) {
    // 更新状态
    this.isHidePanel = val;
  }

  @Watch('uploadFileList', { immediate: true, deep: true })
  onValueChange() {
    // 更新状态
    this.uploadFileStatus();
  }

  getIconPic(format: string) {
    return defaultIconPicConfig[format.toUpperCase()];
  }

  formatLen(value: string, limit: number) {
    return lenEllipsisFun(value, limit);
  }

  formatSize(size: string) {
    return normalizeSizeFun(size);
  }

  uploadFileStatus() {
    const getUList = this.uploadFileList;
    this.successUploadFileList = getUList.filter(f => f.status === 'success');
    this.failUploadFileList = getUList.filter(f => f.status === 'fail');
  }

  // 格式化上传进度
  formatProgress(progress: number) {
    if (progress) {
      return `${Number.parseFloat(progress.toFixed(2))}%`;
    }
    return '排队中...';
  }

  closePanel() {
    this.$emit('close');
  }
}
