
















import { Component, Vue, Prop } from 'vue-property-decorator';

@Component
export default class rightClickMenu extends Vue {
  $refs!: {
    popMenuDom: any;
  };

  @Prop({
    type: Number,
    default: 0,
  })
  differY!: number;

  @Prop({
    type: Number,
    default: 0,
  })
  differX!: number;

  showMenuFlag: boolean = false;

  showMenu(e: MouseEvent) {
    this.$emit('rightClick');
    const popMenuElement = this.$refs.popMenuDom;
    if (popMenuElement) {
      this.$nextTick(() => {
        const left = e.pageX;
        const winWidth = document.body.offsetWidth;
        const menuWidth = popMenuElement.offsetWidth;
        if (left + menuWidth > winWidth) {
          popMenuElement.style.left = `${
            winWidth - menuWidth - 5 - this.differX || 0
          }px`;
        } else {
          popMenuElement.style.left = `${left}px`;
        }

        const top = e.pageY;
        const menuHeight = popMenuElement.offsetHeight;
        const winHeight = window.innerHeight;
        if (top + menuHeight > winHeight) {
          popMenuElement.style.top = `${
            winHeight - menuHeight - 5 - this.differY || 0
          }px`;
        } else {
          popMenuElement.style.top = `${top}px`;
        }

        this.showMenuFlag = true;
      });
      document.documentElement.addEventListener('mousedown', this.cancelClick);
    }
  }

  cancelClick() {
    setTimeout(() => {
      this.showMenuFlag = false;
    }, 150);
    document.documentElement.removeEventListener('mousedown', this.cancelClick);
  }
}
