






















































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import PopDialog from './popDialog.vue';
import { FolderNode, MaterialItem } from '../common/js/typing';

@Component({
  components: {
    PopDialog
  }
})
export default class materialContent extends Vue {
  @Prop({
    type: Array,
    default: ''
  })
  trueTreeFolder!: Array<FolderNode>;

  showSearchPannel: boolean = false;

  searchName: string = '';

  choseFolderId: number = -1;

  @Prop({
    type: Boolean,
    default: false
  })
  initTree!: boolean;

  @Watch('initTree')
  onInitTreeChange() {
    this.searchName = '';
    this.choseFolderId = -1;
  }

  inputFn() {
    if (this.searchName.length > 0) {
      this.showSearchPannel = true;
    } else {
      this.showSearchPannel = false;
    }
  }

  get folderPathArr() {
    const name = this.searchName;
    // let searchArr = [];
    if (name.length > 0) {
      const folderArr = this.trueTreeFolder;
      const checkFolders = this.getCheckFolders({}, folderArr, name, []);
      const folderPathArr: any = [];
      // 获取到所有上级文件夹
      for (let i = 0; i < checkFolders.length; i += 1) {
        folderPathArr.push({
          path: this.getFolderPath(checkFolders[i]),
          id: checkFolders[i].id
        });
      }
      if (folderPathArr.length > 0) {
        return folderPathArr;
      }
      return [
        {
          path: '没有找到该目录',
          id: -1
        }
      ];
    }
    return [];
  }

  getFolderPath(checkFolders: FolderNode) {
    const stack = [];
    let path = '';
    stack.push(checkFolders);
    while (stack.length > 0) {
      const arr = stack.pop();
      path += `/${arr && arr.name}`;
      if (arr && arr.parent?.name) {
        stack.push(arr.parent);
      }
    }
    return path
      .split('/')
      .splice(1)
      .reverse()
      .join('/');
  }

  getCheckFolders(
    parent: any,
    folderArr: Array<FolderNode>,
    name: string,
    result: Array<FolderNode>
  ) {
    for (let i = 0; i < folderArr.length; i += 1) {
      const arr = folderArr[i];
      arr.parent = parent;
      if (arr.name.indexOf(name) >= 0) {
        result.push(arr);
      }
      if (arr.children) {
        this.getCheckFolders(arr, arr.children || [], name, result);
      }
    }
    return result;
  }

  // 获取文件夹id
  getCurFolderId(id: number, path: string) {
    this.showSearchPannel = false;
    this.choseFolderId = id;
    const name = path !== '没有找到该目录' ? path.split('/').pop() : '';
    this.searchName = name || '';
    this.$emit('chosenFolderId', id);
  }

  handleChooseFolder(item: MaterialItem) {
    this.choseFolderId = item.id;
    this.$emit('chosenFolderId', item.id);
  }
}
