


































































































































































































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import upload from './upload.vue';
import rightClickMenu from './rightClickMenu.vue';
import detailDrawer from './detailDrawer.vue';
import PopDialog from './popDialog.vue';
import folderTree from './folderTreeDialog.vue';
import previewPanel from './previewPanel.vue';
import { isHavePreviewUrl, defaultIconPicConfig, normalizeNameFun } from '../common/js/config';
import { MaterialFile, MaterialItem, NestTagOptions } from '../common/js/typing';

interface relatedObj {
  [key: string]: string;
}

@Component({
  name: 'materialContent',
  components: {
    upload,
    rightClickMenu,
    detailDrawer,
    PopDialog,
    previewPanel,
    folderTree
  }
})
export default class materialContent extends Vue {
  $refs!: {
    table: any;
  };

  @Prop({
    type: Boolean,
    default: false
  })
  loading!: boolean;

  @Prop({
    type: String,
    default: 'common'
  })
  showType!: string;

  @Prop({
    type: Array,
    default: []
  })
  materialList!: Array<MaterialItem>;

  @Prop({
    type: Array,
    default: []
  })
  permissionList!: Array<string>;

  @Prop({
    type: Array,
    default: []
  })
  tableHandleList!: Array<string>;

  @Prop({
    type: Array,
    default() {
      return [];
    }
  })
  optionsTags!: Array<NestTagOptions>;

  @Watch('checkMList')
  onCheckMListChange(newVal: Array<number>) {
    const genLen = newVal.length;
    this.$emit('getSelectFiles', newVal);
    // 单选
    if (genLen === 0 || genLen === 1) {
      this.menuList = this.allMenuList;
    }
    // 多选
    if (genLen > 1) {
      this.menuList = this.ctrlMenuList;
    }
  }

  @Watch('selectAllIds')
  onValueChange() {
    this.checkMList = this.materialList.map((item: MaterialItem) => item.id);
    if (this.showType === 'table') {
      this.materialList.forEach((item: MaterialItem) => {
        this.$refs.table.setSelection(item, true);
      });
    }
  }

  @Watch('selectNoneIds')
  onSelectNoneIdsChange() {
    this.checkMList = [];
    if (this.showType === 'table') {
      this.$refs.table.setClearSelection();
    }
  }

  @Watch('curFolderId')
  onCurFolderIdChange() {
    this.checkMList = [];
  }

  @Watch('$parent.$data.tableHeight')
  onTableHeightChange(newVal: number) {
    this.tableHeight = newVal - 32 || newVal;
  }

  saveClickFun!: Function; // 上传组件的点击响应方法

  clickTimer: number = -1;

  tableHeight: number = 0;

  menuList: Array<string> = [];

  checkMList: Array<number> = [];

  // 素材预览
  showPreviewFlag: boolean = false;

  initTree: boolean = false;

  previewIndex: number = -1;

  reNameInput: string = ''; // 重命名素材输入框值

  openDetailFLag: boolean = false; // 是否打开素材详情弹窗

  openDetailIdArr: number[] = []; // 打开详情的素材id集合

  openDetailType: string = ''; // 打开详情的情况（'detail' / 'tag'）

  detailMaterialItem: MaterialItem | string = ''; // 查看详情的素材

  chosenFolderId: number = -1;

  reNameItem: MaterialItem | string = ''; // 重命名素材

  chooseFolderType: string = '';

  // 重命名素材弹窗
  showReNameDialog: boolean = false;

  // 选择文件夹弹窗
  showChooseFolderDialog: boolean = false;

  get allMenuList() {
    const allMenu: any = {
      copy: '复制到文件夹',
      move: '剪切至文件夹',
      download: '导出文件',
      rename: '重命名',
      del: '删除'
    };
    const result: Array<string> = [];
    this.permissionList.forEach(item => {
      if (allMenu[item]) {
        if (item === 'move') {
          if (this.isInFolder) {
            result.push(allMenu[item]);
          }
        } else {
          result.push(allMenu[item]);
        }
      }
    });
    result.unshift('在新窗口打开');
    result.push('添加标签');
    return result;
  }

  get ctrlMenuList() {
    const ctrlMenu: any = {
      del: '删除',
      download: '导出文件',
      move: '剪切至文件夹'
    };
    const result: Array<string> = [];
    this.permissionList.forEach(item => {
      if (ctrlMenu[item]) {
        if (item === 'move') {
          if (this.isInFolder) {
            result.push(ctrlMenu[item]);
          }
        } else {
          result.push(ctrlMenu[item]);
        }
      }
    });
    result.push('添加标签');
    return result;
  }

  get tableMenuList() {
    let allMenu: relatedObj = {};
    // 筛选有权限的操作选项
    const pMenu: relatedObj = {
      copy: '复制到文件夹',
      move: '剪切至文件夹',
      download: '导出文件',
      rename: '重命名',
      del: '删除'
    };
    this.permissionList.forEach(item => {
      if (pMenu[item]) {
        if (item === 'move') {
          if (this.isInFolder) {
            allMenu[item] = pMenu[item];
          }
        } else {
          allMenu[item] = pMenu[item];
        }
      }
    });
    // 补充不受权限控制的操作选项
    allMenu = Object.assign(allMenu, {
      preview: '预览',
      open: '在新窗口打开',
      tag: '添加标签'
    });
    // 筛选可支持的操作选项
    const result: Array<string> = [];
    this.tableHandleList.forEach(l => {
      if (allMenu[l]) {
        result.push(allMenu[l]);
      }
    });
    return result;
  }

  get canUpload() {
    return this.permissionList.includes('add');
  }

  get height() {
    return this.$parent.$props.height;
  }

  get width() {
    console.log(this.$parent.$data.width);
    return this.$parent.$data.width;
  }

  get treeFolder() {
    return this.$parent.$data.treeFolder;
  }

  get selectAllIds() {
    return this.$parent.$data.selectAllIds;
  }

  get selectNoneIds() {
    return this.$parent.$data.selectNoneIds;
  }

  get curFolderId() {
    return this.$parent.$data.curFolderId;
  }

  get isInFolder() {
    return this.curFolderId && this.curFolderId !== 0;
  }

  get curFolderName() {
    return this.$parent.$data.curFolderName;
  }

  get trueTreeFolder() {
    return this.treeFolder;
  }

  emitClickFun(saveClickFun: Function) {
    this.saveClickFun = saveClickFun;
  }

  formatButtonName(menu: string) {
    const obj: relatedObj = {
      复制到文件夹: '复制',
      剪切至文件夹: '移动',
      导出文件: '导出文件',
      重命名: '重命名',
      删除: '删除',
      预览: '预览',
      在新窗口打开: '在新窗口打开',
      添加标签: '添加标签'
    };
    return obj[menu];
  }

  handleTableSelectChange(selectConfig: Array<MaterialItem>) {
    const getIds = selectConfig.map(item => item.id);
    this.checkMList = getIds;
  }

  // 处理表格展示形式的素材操作
  handleClickTableMenu(item: MaterialItem, menu: string) {
    if (menu === '预览') {
      this.previewThis(item);
    } else {
      this.checkMList = [item.id];
      this.handleClickMenu(item, menu);
    }
  }

  // 处理素材操作
  handleClickMenu(item: MaterialItem, menu: string) {
    if (this.checkMList.length > 0) {
      if (menu === '在新窗口打开') {
        window.open(item.original_url);
        this.checkMList = [];
      } else if (menu === '复制到文件夹') {
        this.initTree = true;
        this.showChooseFolderDialog = true;
        this.chooseFolderType = '复制至文件夹';
        this.chosenFolderId = -1;
      } else if (menu === '剪切至文件夹') {
        this.initTree = true;
        this.showChooseFolderDialog = true;
        this.chooseFolderType = '剪切至文件夹';
        this.chosenFolderId = -1;
      } else if (menu === '导出文件') {
        this.exportMaterial();
      } else if (menu === '重命名') {
        if (this.checkMList.length === 1) {
          const getItem = this.materialList.filter(o => o.id === this.checkMList[0]);
          this.reNameItem = getItem[0] as MaterialItem;
          this.reNameInput = '';
          this.showReNameDialog = true;
          this.checkMList = [];
        } else {
          this.$message({
            message: '请选择一个素材',
            type: 'warning'
          });
        }
      } else if (menu === '删除') {
        this.deleteMaterial();
      } else if (menu === '添加标签') {
        this.openAddTag();
      }
    }
  }

  // 导出素材
  exportMaterial() {
    const getCList = this.checkMList;
    this.$emit('exportFile', getCList);
    this.checkMList = [];
  }

  handleChooseFolderDialogCancel() {
    this.showChooseFolderDialog = false;
    this.initTree = false;
    this.checkMList = [];
  }

  // 添加/移动素材
  confirmFolderDialog() {
    this.initTree = false;
    if (this.chosenFolderId !== -1) {
      // 复制到文件夹
      if (this.chooseFolderType === '复制至文件夹') {
        if (this.checkMList.length === 1) {
          this.$emit('copyFile', this.checkMList[0], this.chosenFolderId, (value: string) => {
            if (value === 'success') {
              this.showChooseFolderDialog = false;
              this.checkMList = [];
            }
          });
        } else {
          this.$message({
            message: '请选择一个素材',
            type: 'warning'
          });
        }
      }
      // 剪切至文件夹
      if (this.chooseFolderType === '剪切至文件夹') {
        this.$emit('moveFile', this.checkMList, this.chosenFolderId, (value: string) => {
          if (value === 'success') {
            this.showChooseFolderDialog = false;
            this.checkMList = [];
          }
        });
      }
    } else {
      this.$message({
        message: '请选择文件夹',
        type: 'warning'
      });
    }
  }

  // 删除素材
  deleteMaterial() {
    this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
      .then(() => {
        this.$emit('delFile', this.checkMList, (value: string) => {
          console.log(value);
          this.checkMList = [];
        });
      })
      .catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
        this.checkMList = [];
      });
  }

  // 增加上传任务
  addTask(param: any) {
    this.$emit('uploadFile', param);
  }

  // 响应上传状态变化
  handleFileStatusChange(successFlag: boolean, file: MaterialFile, reason?: string) {
    const getFile = file;
    if (successFlag) {
      getFile.status = 'success';
      // this.successFileList.push(getFile);
    } else {
      getFile.status = 'fail';
      getFile.reason = reason || '上传失败';
      // this.failFileList.push(getFile);
    }
    // 若队列里全部任务处理完成
    // if (this.failNum + this.successNum === this.uploadNum) {
    //   this.getAllData();
    // }
  }

  checkPreview(format: string) {
    return isHavePreviewUrl(format);
  }

  getPreviewUrl(item: MaterialItem) {
    if (item.format.toUpperCase() === 'GIF') {
      return item.original_url;
    }
    return item.preview_url;
  }

  getIconPic(format: string) {
    return defaultIconPicConfig[format.toUpperCase()];
  }

  // 处理单选和多选素材
  handleMaterialClick(item: MaterialItem) {
    clearTimeout(this.clickTimer);
    this.clickTimer = setTimeout(() => {
      const getId = item.id;
      const getIndex = this.checkMList.indexOf(getId);
      const getCMLen = this.checkMList.length;
      // 未有选中时
      if (getCMLen === 0) {
        this.checkMList.push(getId);
      }
      // 已选中一个时
      if (getCMLen === 1) {
        // 直接单击 或 按住ctrl 都是多选
        if (getIndex === -1) {
          this.checkMList.push(getId);
        } else {
          this.checkMList.splice(getIndex, 1);
        }
        // // 按住ctrl
        // if (this.isClickCtrl) {
        //   if (getIndex === -1) {
        //     this.checkMList.push(getId);
        //   } else {
        //     this.checkMList.splice(getIndex, 1);
        //   }
        // }
        // // 直接点击
        // if (!this.isClickCtrl) {
        //   this.checkMList = getIndex === -1 ? [getId] : [];
        // }
      }
      // 已选中多个时
      if (getCMLen > 1) {
        // 直接单击 或 按住ctrl 都是多选
        if (getIndex === -1) {
          this.checkMList.push(getId);
        } else {
          this.checkMList.splice(getIndex, 1);
        }
      }
      // } else {
      //   this.$message({
      //     message: '该素材不在所选内存大小范围内，请重新选择',
      //     type: 'warning'
      //   });
      // }
    }, 300);
  }

  // 预览素材
  previewMaterial(index: number) {
    clearTimeout(this.clickTimer);
    this.previewIndex = index;
    this.showPreviewFlag = true;
  }

  previewThis(item: MaterialItem) {
    let getIndex = -1;
    this.materialList.forEach((o: MaterialItem, index: number) => {
      if (o.id === item.id) {
        getIndex = index;
      }
    });
    if (getIndex !== -1) {
      this.previewMaterial(getIndex);
    }
  }

  getChosenFolderId(id: number) {
    this.chosenFolderId = id;
  }

  closeDetail() {
    this.detailMaterialItem = '';
    this.openDetailFLag = false;
  }

  renameMaterial(renameValue: string, item: MaterialItem) {
    this.confirmReNameDialog(renameValue, item, 'detail');
  }

  getRecentTags(callback: Function) {
    this.$emit('getRecentTags', callback);
  }

  setTag(tag_ids: Array<number>, mIds: Array<number>, callback: Function) {
    this.$emit('setTag', tag_ids, mIds, callback);
  }

  handleRightClick(item: MaterialItem) {
    // 未选择或已选一个时，响应选中
    if (this.checkMList.length <= 1) {
      this.checkMList = [item.id];
    }
  }

  // 重命名素材
  confirmReNameDialog(renameValue: string, item: MaterialItem, type: string) {
    const getItem = item;
    let getReNameInput = renameValue;
    if (getReNameInput.trim().length === 0) {
      this.$message({
        message: '新名称不能为空',
        type: 'error'
      });
      this.reNameInput = '';
    } else {
      getReNameInput = normalizeNameFun(getReNameInput);
      this.$emit('renameFile', getItem.id, getReNameInput, (value: string) => {
        if (value === 'success') {
          if (type === 'rightClick') {
            this.showReNameDialog = false;
            this.checkMList = [];
          }
          if (type === 'detail') {
            this.closeDetail();
          }
        }
      });
    }
  }

  openDetail(item: MaterialItem) {
    this.$emit('getFileDetail', item.id, (value: MaterialItem) => {
      this.detailMaterialItem = value;
      if (this.detailMaterialItem && typeof this.detailMaterialItem === 'object') {
        this.detailMaterialItem.preview_url = item.preview_url;
        this.detailMaterialItem.original_url = item.original_url;
        this.openDetailIdArr = [item.id];
        this.openDetailType = 'detail';
        this.openDetailFLag = true;
      } else {
        this.$message.error('获取详情信息的格式可能有误，请检查');
      }
    });
  }

  openAddTag() {
    const getC = this.checkMList;
    const len = getC.length;
    // 若选择多个素材，则显示的是最后一个素材的信息
    const getDetailId = getC[len - 1];
    const getDetailItem = this.materialList.filter(o => o.id === getDetailId)[0];
    const popDrawer = () => {
      this.detailMaterialItem = getDetailItem;
      this.$forceUpdate();
      this.openDetailIdArr = getC;
      this.openDetailType = 'tag';
      this.openDetailFLag = true;
      this.checkMList = [];
    };
    if (len === 1) {
      // 选择一个素材时，查询该素材已有的标签
      this.$emit('getFileDetail', getDetailId, (value: MaterialItem) => {
        getDetailItem.tag_info = value.tag_info;
        popDrawer();
      });
    } else {
      // 选择多个素材时
      getDetailItem.tag_info = [];
      popDrawer();
    }
  }
}
